* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.news-form {
  width: 100%;
  height: 100%;
}

input[type="text"] {
  width: 500px;
  height: 40px;
  transition: width .4s ease-in-out;
  display: block;
}

input[type="text"]:focus {
  width: 100%;
}

button[type="submit"] {
  width: 150px;
  height: 40px;
  cursor: pointer;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 15px;
  margin-top: 20px;
  padding: 14px 15px;
  font-size: 16px;
  display: block;
}

.news-loading {
  text-align: center;
  display: none;
}

.show {
  display: block;
}

